
import global from '../assets/globalEvent.js';
import tools from '../../../lib/tools.js';
import healthy from '../../../lib/healthy.js';
import recordsBox from '../../../components/RecordsBox.vue';
import Toast from 'vux/src/components/toast/index.vue'

export default {
name:'recordIndex',
props:['message'],
components:{recordsBox,Toast},
data(){
return {
managerId:'',
msg:'默认值',
services:[],
showToast:false,
toastMsg:''
}
},
created(){
console.log(111);
this.managerId = (localStorage.getItem('md') || tools.getRequestParam('managerId') || tools.getCookie('user_id'));
if(!this.managerId || this.managerId=='undefined'){
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
return false;
}
this.getNumberMouth();
},
mounted(){
let _this = this;
global.$on('username',(msg) => {
console.log('msg',msg);
_this.msg = msg;
})
},
methods:{

getStatus(obj){
switch (obj.dataType){
case "HR":
return healthy.heartRateReport(obj.value).status;
case "BO":
return healthy.bloodOxygenReport(obj.value).status;
case "BMI":
return healthy.weightHandle(obj.heValue,obj.weValue).status;
case "BP":
return healthy.bloodPressureReport(obj.dia,obj.sys).status;
case "BG":
return healthy.bloodSugarReport(obj.value).status;
case "UA":
return healthy.uaReport(obj.value).status;
case "TE":
return healthy.tempReport(obj.value).status;
case "WP":
return healthy.wpReport(obj.value).status;
case "BONE":
return healthy.boneReport(obj.value).status;
case "BMR":
return healthy.bmrReport(obj.value).status;
case "CHOL":
return healthy.cholReport(obj.value).status;
case "BHB":
return healthy.hbReport(obj.value).status;
case "MUCALE":
return healthy.muscleReport(obj.weight,obj.value).status;
case "PIW":
return healthy.piwReport(obj.weight,obj.value).status;
case "FP":
return healthy.fpReport(obj.value).status;
case "BF":
return healthy.cholReport(obj.value).status;
default:
break;
}
},
getNumberMouth(time){
let vm = this;
let managerId = (localStorage.getItem('md') || tools.getCookie('user_id'));

this.$http.axios({method:'get',url:'/deviceData/getMeasureData?managerId='+managerId+'&time='}).then((result) => {
result = result.data;
if(result.successful && (result.status==200)){
console.log(JSON.stringify(result)+"@@");
for(let i = 0;i<result.data.length;i++){
let status = this.getStatus(result.data[i]);
this.$set(result.data[i],'status',status);
if(result.data[i].dataType == "CRP"){
result.data[i].name = "全量程C反应蛋白";
}
}
vm.services = result.data;
console.log(this.services);
}else{
vm.services = [];
vm.showToast = true;
vm.toastMsg = result.resultCode.message;
console.log(result);
}
},(error) => {
console.log(error);
});
}
}
}



