
import tools from '../../lib/tools.js';
import recordTimeLine from './components/recordTimeLine';
import recordIndex from './components/recordIndex';
import tabBar from '../../components/TabBar';
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'

export default {
name:'records',
data(){
return {
managerId:'',
info:{},
tabBar:false,
addTab:true,
showPup:false,
showDialog:false,
showToast:false,
toastMsg:'',
showConfirm:false,
saveType:'',
height:'',
itemRadioBoxChecked:'itemRadioBoxChecked',
itemRadioBox:'itemRadioBox',
medicineUnit:['粒','片','瓶','克g','毫克mg','毫升ml'],
sleepStatus:['好','不好'],
sleepDesc:['失眠','易醒','多梦','醒后难入睡'],
drinkType:['白酒','啤酒','黄酒','红酒','洋酒'],
sportType:['跑步','健走','游泳','跳绳','骑行','力量训练','足球','篮球','羽毛球','网球'],
sportTime:['0.5小时','1小时','1.5小时','2小时','3小时'],
isRecordInfo:{
weValue:false,heValue:false,
dbpValue:false,sbpValue:false,
boValue:false,
hrValue:false,
snValue:false,
bgValue:false,
sleep:false,
drink:false,
smoke:false,
medicine:false,
laboratory:false,
medical:false,
abnormal:false,
sport:false,
},
addRecordInfo:{
bmi:{weValue:"",heValue:""},
dbpValue:'',sbpValue:'',
boValue:'',
hrValue:'',
snValue:'',
bgValue:'',
sleep:{time:'',status:'好',desc:[]},
drink:{quantity:'',type:'',desc:''},
smoke:{quantity:'',desc:''},
medicine:[],
laboratory:{name:'',time:'',imgUrl:[]},
medical:{name:'',time:'',imgUrl:[],desc:''},
abnormal:{title:'',time:'',imgUrl:[],desc:''},
sport:{type:'',time:'',imgUrl:[],desc:''},
},
showSelectItems:false,
selectItems:[],
}
},
components:{recordTimeLine,recordIndex,Tab,TabItem,XDialog,Cell,Popup,Group,XInput,Confirm,Toast,Selector,Checker, CheckerItem,XTextarea,Datetime,CellBox,tabBar},
directives: {TransferDom},
mounted(){
this.managerId = (localStorage.getItem('md') || tools.getRequestParam('managerId') || tools.getCookie('user_id'));
console.log(this.managerId);
if(!this.managerId || this.managerId=='undefined'){
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
return false;
}
if(window.location.href.indexOf('recordTimeLine')>=0){
this.tabBar = true;
}
var vm = this;
this.$http.post('/apiUser/queryAccount?managerId='+this.managerId,{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data){
vm.info = data.data;
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
},
methods:{
showChildMsg(msg){
alert(msg)
},
addClass(e){
this.tabBar = (e=='one')?false:true;
},
searchByDate(){
tools.next('/m/equipment.html');


},
addRecord(){

this.showPup = !this.showPup;
},
showTab(num){

this.addTab = !this.addTab;
},
addRecordText(type){
if(type=='bmi'){//体重   bmi
this.isRecordInfo.weValue = true;

if(localStorage.getItem('height')){
this.isRecordInfo.heValue = false;
}else{
this.isRecordInfo.heValue = true;
}
}else if(type=='bp'){//血压
this.isRecordInfo.dbpValue = true;
this.isRecordInfo.sbpValue = true;
}else if(type=='bo'){//血氧
this.isRecordInfo.boValue = true;
}else if(type=='hr'){//心率
this.isRecordInfo.hrValue = true;
}else if(type=='sn'){//步数
this.isRecordInfo.snValue = true;
}else if(type=='bg'){//血糖
this.isRecordInfo.bgValue = true;
}else if(type=='sleep'){

this.isRecordInfo[type] = true;
}else if(type=='drink'){// 饮酒
this.isRecordInfo[type] = true;

}else if(type=='smoke'){// 吸烟
this.isRecordInfo[type] = true;

}else if(type=='medicine'){//服药
this.isRecordInfo[type] = true;

}else if(type=='laboratory'){// 化验单
this.isRecordInfo[type] = true;

}else if(type=='medical'){// 医学影像
this.isRecordInfo[type] = true;

}else if(type=='abnormal'){// 异常情况
this.isRecordInfo[type] = true;

}else if(type=='sport'){// 运动
this.isRecordInfo[type] = true;
}
this.showDialog = true;
this.saveType = type;
console.log(type);
},
saveRecordText(){
let vm = this;
let tempArr = this.isRecordInfo;
let params = {};
let type = '';
for(let key in tempArr){
if(tempArr[key]){
if(this.addRecordInfo[key]){
type = key;
if(tools.isObj(this.addRecordInfo[key])){
params[key] = tools.deepCopy(this.addRecordInfo[key]);
}else if(tools.isArray(this.addRecordInfo[key])){
params[key] = tools.deepCopy(this.addRecordInfo[key]);
}else{
params[key] = this.addRecordInfo[key];
}
}else{



}

}
}
if(this.isRecordInfo.dbpValue && this.isRecordInfo.sbpValue){
if(!(params.sbpValue&&params.dbpValue)){
this.showToast = true;
this.toastMsg = '请将页面信息填写完整';
return false;
}
}
if(this.isRecordInfo.weValue && this.isRecordInfo.heValue){
if(!(params.weValue&&params.heValue)){
this.showToast = true;
this.toastMsg = '请将页面信息填写完整';
return false;
}
}
if(this.isRecordInfo.weValue && (!this.isRecordInfo.heValue)){
if(params.weValue){
params.heValue = localStorage.getItem('height');
}else{
this.showToast = true;
this.toastMsg = '请将页面信息填写完整';
return false;
}
}

console.log('params',params);
if(Object.keys(params).length == 0){
this.showToast = true;
this.toastMsg = '请将页面信息填写完整';
return false;
}

for(let key in params){
if(params[key] == ''){
this.showToast = true;
this.toastMsg = '请将页面信息填写完整';
return false;
}
}

for(let key in tempArr){
if(tempArr[key]){
if(this.addRecordInfo[key]){
type = key;
if(tools.isObj(this.addRecordInfo[key])){
params[key] = tools.deepCopy(this.addRecordInfo[key]);
for(let item in this.addRecordInfo[key]){
if(tools.isObj(this.addRecordInfo[key][item])){
for(let thr in this.addRecordInfo[key][item]){
this.addRecordInfo[key][item][thr] = '';
}
}else if(tools.isArray(this.addRecordInfo[key][item])){
for(let i=0;i<this.addRecordInfo[key][item].length;i++){
this.addRecordInfo[key][item][i] = '';
}
}else{
this.addRecordInfo[key][item] = '';
}
}
}else if(tools.isArray(this.addRecordInfo[key])){
params[key] = tools.deepCopy(this.addRecordInfo[key]);
this.addRecordInfo[key].length = 0;
}else{
params[key] = this.addRecordInfo[key];
this.addRecordInfo[key] = '';
}
}
this.isRecordInfo[key] = false;
}
}
console.log(params);

this.$http.post('/deviceData/insertMeasureData',{managerId:this.managerId,type:this.saveType,data:JSON.stringify(params)}).then((result) => {
result = result.data;
console.log(result);
if(result.resultCode && result.resultCode.code=='SUCCESS'){
vm.showToast = true;
vm.toastMsg = '保存成功';
vm.saveType = '';
vm.showDialog = false;
if(params.heValue){
vm.updateInfo('height',params.heValue);  // 保存体重
localStorage.setItem('height',params.heValue);
}
window.location.reload();
}else{
console.error(result);
vm.showToast = true;
vm.toastMsg = '保存失败';
}
},(error) => {
console.error(error);
vm.showToast = true;
vm.toastMsg = '保存失败';
})
},
closeRecordText(){
let tempArr = this.isRecordInfo;
for(let key in tempArr){
if(tempArr[key]){
if(tools.isObj(this.addRecordInfo[key])){
for(let item in this.addRecordInfo[key]){
if(tools.isObj(this.addRecordInfo[key][item])){
for(let three in this.addRecordInfo[key][item]){
if(this.addRecordInfo[key][item][three]){
this.addRecordInfo[key][item][three] = '';
}
}
}else if(tools.isArray(this.addRecordInfo[key][item])){
for(let i=0;i<this.addRecordInfo[key][item].length;i++){
if(this.addRecordInfo[key][item][i]){
this.addRecordInfo[key][item][i] = '';
}
}
}else if(this.addRecordInfo[key][item]){
this.addRecordInfo[key][item] = '';
}
}
}else if(tools.isArray(this.addRecordInfo[key])){
for(let i=0;i<this.addRecordInfo[key].length;i++){
if(this.addRecordInfo[key][i]){
this.addRecordInfo[key][i] = '';
}
}
}else{
if(this.addRecordInfo[key]){
this.addRecordInfo[key] = '';
}
}
tempArr[key] = false;
}
}
this.showDialog = false;
},
onJGConfirm(msg){
let tempArr = this.isRecordInfo;
for(let key in tempArr){
if(tempArr[key]){
tempArr[key] = false;
}
}
this.showDialog = false;
},
fileUpload(e,key,number){

let vm = this;
console.log(e);
let file = e.target.files[0];
let param = new FormData(); //创建form对象
param.append('file',file,file.name);//通过append向form对象添加数据


let config = {
headers:{'Content-Type':'multipart/form-data'}
};  //添加请求头
this.$http.axiosPost('/file/upload',param,config).then(response=>{
console.log(response.data);
response = response.data;
if(response.resultCode.code=='SUCCESS' && response.data){
if(key=='medicine'){
vm.addRecordInfo[key][number].imgUrl.push({src:response.data});
}else{
vm.addRecordInfo[key].imgUrl.push({src:response.data});
}








}else{
alert('上传失败');
}
})
},
addMedicineBox(){
this.addRecordInfo.medicine.push({name:'',time:'',imgUrl:[],measure:'',unit:''});
},
updateInfo(type,val){
var vm = this;
if(!this.managerId){return}
let data = {managerId:vm.managerId};
data[type] = val;
if(type=='height'){
localStorage.setItem('height',val);
}
this.$http.axios({method:'POST',url:'/apiUser/update',data:data}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200) && data.resultCode){
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}else{
console.error(data);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '操作失败';
})
},
checker(e,desc,obj,item){
if(e.target.className==''){
e.target.className += 'active'
}else{
e.target.className = ''
}
if(this.addRecordInfo[obj][item]){
if(this.addRecordInfo[obj][item].indexOf(desc)<0){
this.addRecordInfo[obj][item].push(desc);
}else{
this.addRecordInfo[obj][item].splice(this.addRecordInfo[obj][item].indexOf(desc),1);
}
}else{
this.addRecordInfo[obj][item] = [desc];
}
},
goTo(){
tools.next('/m/personalInfo.html');
}
}
}



