
import tools from '../lib/tools.js';
const FastClick = require('fastclick')
FastClick.attach(document.body);
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
name:'tabBar',
components:{Flexbox, FlexboxItem,Toast },
props:['checked'],
data(){
return {
showToast:false,
toastMsg:'',
isNothingMessage:false
}
},
mounted(){
this.initMessage();
},
methods:{
goTo(title){
let url = '';
switch(title){
case 'home' : url = '/index.html'; break;
case 'file' : url = '/healthRecords.html'; break;
case 'message' : url = '/messages.html'; break;
case 'myservice' : url = '/myService.html'; break;
case 'personal' : url = '/personal.html'; break;
}
window.location.href= '/m'+url + '?=_' +(new Date().getTime());
},
initMessage(){
let id = (localStorage.getItem('md') || tools.getCookie('user_id'));//用户id
if((!id) || id=='undefined'){
return false;
}
let vm = this;
this.$http.post('/message/countAllUnReadMessage',{userId:id}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data && data.data.unReadCount){
vm.isNothingMessage = true;
}
}else{
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
}
}
}



